interface Location {
    name: string;
    latitude: number;
    longitude: number;
  }
  
// Function to calculate the distance between two points using the Haversine formula
function calculateDistance({
  lat1, lon1, lat2, lon2
}: {
    lat1: number, lon1: number, lat2: number, lon2: number
  }): number {
  const earthRadius = 6371; // Radius of the earth in kilometers
  
  const dLat = toRadians(lat2 - lat1);
  const dLon = toRadians(lon2 - lon1);
  
  const a =
      Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(toRadians(lat1)) * Math.cos(toRadians(lat2)) * Math.sin(dLon / 2) * Math.sin(dLon / 2);
  
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  const distance = earthRadius * c;
  
  return distance;
}
  
// Function to convert degrees to radians
function toRadians(degrees: number): number {
  return (degrees * Math.PI) / 180;
}
  
// Function to filter locations within a given radius from a target location
export function filterLocationsByRadius(
  {
    locations,
    targetLatitude,
    targetLongitude,
    radius
  } : {
    locations: Location[],
    targetLatitude: number,
    targetLongitude: number,
    radius: number
    }
): Location[] {
  // return locations.filter(location => {
  //   const distance = calculateDistance({
  //     lat1 : targetLatitude, lon1 : targetLongitude, lat2 : location.latitude, lon2 : location.longitude
  //   }
  //   );
  //   return distance <= radius;
  // });

  const locationDistances = locations.map(location => {
    const distance = calculateDistance({
      lat1 : targetLatitude, lon1 : targetLongitude, lat2 : location.latitude, lon2 : location.longitude
    });
    return {location, distance};
  });

  const filteredLocations = locationDistances.filter(
    ({distance}) => distance <= radius
  );

  const sortedLocations = filteredLocations.sort(
    (a, b) => a.distance - b.distance
  );

  return sortedLocations.map(({location}) => location);
}
  


  