/**
 * Utility class for working with localStorage in TypeScript.
 */
export class LocalStorageUtil {
  /**
     * Retrieves an item from localStorage.
     * @param key - The key of the item.
     * @returns The value associated with the key, or null if the key doesn't exist.
     */
  static getItem(key: string): string | null {
    try {
      return localStorage.getItem(key);
    } catch (error) {
      // eslint-disable-next-line no-console
      console.error('Error retrieving item from localStorage:', error);
      return null;
    }
  }
  
  /**
     * Sets an item in localStorage.
     * @param key - The key of the item.
     * @param value - The value to be stored.
     * @returns True if the item was successfully set, false otherwise.
     */
  static setItem(key: string, value: string): boolean {
    try {
      localStorage.setItem(key, value);
      return true;
    } catch (error) {
      // eslint-disable-next-line no-console
      console.error('Error setting item in localStorage:', error);
      return false;
    }
  }
  
  /**
     * Removes an item from localStorage.
     * @param key - The key of the item to be removed.
     * @returns True if the item was successfully removed, false otherwise.
     */
  static removeItem(key: string): boolean {
    try {
      localStorage.removeItem(key);
      return true;
    } catch (error) {
      // eslint-disable-next-line no-console
      console.error('Error removing item from localStorage:', error);
      return false;
    }
  }
  
  /**
     * Clears all items from localStorage.
     * @returns True if localStorage was successfully cleared, false otherwise.
     */
  static clear(): boolean {
    try {
      localStorage.clear();
      return true;
    } catch (error) {
      // eslint-disable-next-line no-console
      console.error('Error clearing localStorage:', error);
      return false;
    }
  }
}

  