import slugify from 'slugify';

import {LocalStorageUtil} from './LocalStorageUtil';
import {COMMUNITIES_LIST, LOCATION_NAMES} from '../constants/constants';
import {BranchProps} from '../pages/branches';
import {Location, StorageKeys} from '../types/common';

export const createSlug = (string: string) => {
  if (!string) {
    return '';
  }
  return slugify(string, {
    strict : true,
    lower  : true,
  });
};


export function parseJSON<T>(jsonString: string): T | null {
  try {
    const parsedData = JSON.parse(jsonString) as T;
    return parsedData;
  } catch (error) {
    // eslint-disable-next-line no-console
    console.error('Error parsing JSON:', error);
    return null;
  }
}

export function getCommunityNameFromLocalStorage(): string | null {
  const communityName = LocalStorageUtil.getItem(StorageKeys.Community);

  if (communityName && COMMUNITIES_LIST.includes(communityName)) {
    return communityName;
  } else {
    LocalStorageUtil.removeItem(StorageKeys.Community);
    return null;
  }
}

export function getSelectedLocationIdFromLocalStorage(): string | null {
  const locationObject = LocalStorageUtil.getItem(StorageKeys.Location);

  if (locationObject) {
    const location: any = parseJSON(locationObject);

    if (location && location?.id && LOCATION_NAMES.some(locationName => locationName.id === location.id)) {
      return location.id.toString();
    } else {
      LocalStorageUtil.removeItem(StorageKeys.Location);
    }
  }

  return null;
}

export function getSelectedBranchesFromLocalStorage(branches: BranchProps[]): BranchProps[] | null {
  const branchObject = LocalStorageUtil.getItem(StorageKeys.Branch);

  if (branchObject) {
    const branch: any = parseJSON(branchObject);

    if (branch) {
      return branch.filter(({code}:{code: string}) =>
        branches.some(mainItem => mainItem.code === code)
      );
    } else {
      LocalStorageUtil.removeItem(StorageKeys.Location);
    }
  }

  return null;
}

export function readSelectedBranchesFromLocalStoragePlain(): BranchProps[] | null {
  const branchObject = LocalStorageUtil.getItem(StorageKeys.Branch);

  if (branchObject) {
    const branch: any = parseJSON(branchObject);
    return branch;
  }

  return null;
}

export function getSelectedLocationFromLocalStorage(): Location | null {
  const locationId = getSelectedLocationIdFromLocalStorage();
  if (locationId) {
    const location = LOCATION_NAMES.find(locationName => locationName.id.toString() === locationId);
    if (location) {
      return location;
    }
  }
  return null;
}