import React, {useEffect, useMemo, useState} from 'react';

import {Container, Alert, Typography} from '@mui/material';
import {PageProps, graphql} from 'gatsby';

import {BranchProps} from './branches';
import CollegeDetailsCard from '../components/CollegeDetailsCard';
import Layout from '../components/layout';
import {CollegeDetailsProps} from '../templates/college-details';
import {Location} from '../types/common';
import {getCommunityNameFromLocalStorage, getSelectedLocationFromLocalStorage, readSelectedBranchesFromLocalStoragePlain} from '../utils/CommonUtils';
import {filterLocationsByRadius} from '../utils/GeofenceUtil';

type DataProps = {
  allStrapiCollege: {
    nodes: CollegeDetailsProps[];
  };
};


const SearchResultsPage = ({
  data: {allStrapiCollege},
}: PageProps<DataProps>) => {
  
  const [selectedCommunity, setSelectedCommunity] = useState('');
  const [selectedLocation, setSelectedLocation] = useState<Location>();
  const [selectedColleges, setSelectedColleges] = useState<CollegeDetailsProps[]>();
  const [selectedBranches, setSelectedBranches] = useState<BranchProps[] | []>([]);

  // console.log('selectedCommunity : ', selectedCommunity);
  // console.log('selectedLocation : ', selectedLocation);
  // console.log('selectedBranches : ', selectedBranches);
  useEffect(() => {
    const communityName = getCommunityNameFromLocalStorage();
    if (communityName) {
      setSelectedCommunity(communityName);
    }
    const selectedLocation = getSelectedLocationFromLocalStorage();
    if (selectedLocation) {
      setSelectedLocation(selectedLocation);
    }
    const selectedBranches = readSelectedBranchesFromLocalStoragePlain();
    if (selectedBranches) {
      setSelectedBranches(selectedBranches);
    }
  }, []);

  const allColleges = useMemo(() => allStrapiCollege.nodes, [allStrapiCollege]);
  const searchCollegesByBranch = useMemo(() => {

    if (!selectedBranches?.length) {
      return allColleges;
    }
    const matchedObjects = new Set();
  
    selectedBranches?.forEach(searchItem => {
      allColleges.forEach(item => {
        item.branches.forEach(branch => {
          if (branch.code === searchItem.code) {
            matchedObjects.add(item);
          }
        });
      });
    });
    
    return Array.from(matchedObjects);
  }, [selectedBranches, allColleges]);

  // useEffect(() => {
  //   if (selectedBranches?.length) {
  //     // console.log('allColleges : ', allColleges)
  //     // console.log('selectedBranches : ', selectedBranches)
  //     console.log('matchedObjects : ', searchCollegesByBranch)
  //     const {latitude, longitude} = selectedLocation;
  //     console.log('222')
  //     if (latitude && longitude && searchCollegesByBranch?.length) {
  //       const filteredLocations = filterLocationsByRadius({
  //         locations       : searchCollegesByBranch as any,
  //         targetLatitude  : latitude,
  //         targetLongitude : longitude,
  //         radius          : 100
  //       }); // Filter locations within a 100 km radius from the target location
  //       setSelectedColleges(filteredLocations as any);
  //     } else {
  //       // console.log('111')
  //       // setSelectedColleges(allColleges)
  //     }
  //   } else {
  //     // console.log('222')
  //     // setSelectedColleges(allColleges)
  //   }
  // }, [searchCollegesByBranch, selectedBranches, selectedLocation]);
  const displayBranchNames = useMemo(() => selectedBranches.map(({name}: any) => name).join(', '), [selectedBranches]);

  useEffect(() => {
    if (selectedLocation) {
      // console.log('allColleges : ', allColleges)
      // console.log('selectedBranches : ', selectedBranches)
      // console.log('matchedObjects : ', searchCollegesByBranch)
      const {latitude, longitude} = selectedLocation;
      // console.log('222')
      if (latitude && longitude && searchCollegesByBranch?.length) {
        const filteredLocations = filterLocationsByRadius({
          locations       : searchCollegesByBranch as any,
          targetLatitude  : latitude,
          targetLongitude : longitude,
          radius          : 100
        }); // Filter locations within a 100 km radius from the target location
        setSelectedColleges(filteredLocations as any);
      } else {
        // console.log('111')
        // setSelectedColleges(allColleges)
      }
    } else {
      // console.log('222')
      // setSelectedColleges(allColleges)
    }
  }, [allColleges, searchCollegesByBranch, selectedLocation]);

  return (
    <Layout headerTitle={'Colleges'} backLink="/" shouldGoBack>
      <Container sx={{my: 4}}>
        
        <Typography variant="h6" component="div" color="secondary">
            Eng. Colleges near by {selectedLocation?.name}
        </Typography>
        <Typography variant="body2" component="div">
          {displayBranchNames}
        </Typography>
          
      </Container>
      {selectedColleges ? (
        <CollegeDetailsCard colleges={selectedColleges}
          selectedBranches={selectedBranches} />
      ) : (

        <Alert icon={false} severity="warning">
  No colleges Found.
        </Alert>
      )}
      
      {/* <Button component={Link} to="/branches" variant="contained" color="primary">
    Branches
</Button>
    <Button component={Link} to="/colleges" variant="contained" color="primary">
    Colleges
</Button> */}
    </Layout>
  );
};


export const query = graphql`
  query {
    allStrapiCollege(sort: {name: ASC}) {
      nodes {
        name
        code
        city
        id
        latitude
        longitude
        address
        branches {
          name
          code
          id
        }
        college_type {
          name
          id
        }
      }
    }
    allStrapiCollegeType(sort: {name: ASC}) {
      nodes {
        name
        id
      }
    }
  }
`;

export default SearchResultsPage;

